import { Component, OnInit } from '@angular/core';
import { PacienteService } from 'src/app/services/paciente.service';
import { Params, ActivatedRoute, Router } from '@angular/router';
import { Paciente } from 'src/app/models/paciente';
import { NavExtrasService } from 'src/app/services/nav-extras.service';
import { Questionario } from 'src/app/models/questionario';

@Component({
  selector: 'app-visualizar-paciente',
  templateUrl: './visualizar-paciente.page.html',
  styleUrls: ['./visualizar-paciente.page.scss'],
})
export class VisualizarPacientePage implements OnInit {

  paciente:Paciente =  new Paciente();
  constructor(private pacienteService:PacienteService, private activatedRouter:ActivatedRoute, 
      private navExtra: NavExtrasService, private router:Router) { }

  async ngOnInit() {
    this.paciente = this.navExtra.get('paciente')
  }

  public editar() {
    this.navExtra.set('paciente', this.paciente);
    this.router.navigateByUrl('/coordenador/pacientes/editar')
  }

}
